@import '../../__data__/style/sizes.module.css';
@import '../../__data__/style/media.module.css';
@import '../../__data__/style/colors.module.css';

.container {
    max-width: 1240px;
    margin: 0 auto;
    padding: 0 var(--offset-sm);
    padding-top: 132px;
}

.productPage {
    background-color: var(--white);
}

.productPageBody {
    margin-top: var(--offset-md2);
    margin-bottom: var(--offset-md2);
    display: flex;
    justify-content: space-between;
}

.productPageImg {
    flex: 0 0 500px;
    height: 500px;
    position: relative;
    margin-right: var(--offset-md2);
}

.productPageImg img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: contain;
}

.productPageRight {
    width: 100%;
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.productPageDescription {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: var(--black);
}

.productPageTitle {
    font-size: 24px;
    line-height: 30px;
}

.productPageLine {
    width: 100%;
    height: 1px;
    background-color: var(--black);
    margin: var(--offset-md2) 0;
}

.productPageLikeWrapper {
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.productPageLikeWrapper span {
    font-size: 18px;
    line-height: 20px;
}

.productPageLikeWrapper button {
    border: none;
    background: transparent;
    cursor: pointer;
    color: var(--black);
    outline-color: var(--green-4);
}

.productPageLikeWrapper button:hover {
    color: var(--green-4);
}

.productPageLikeWrapper button.active {
    border: none;
    background: transparent;
    cursor: pointer;
    color: var(--red-4);
    outline-color: var(--green-4);
}

.productPageLikeWrapper button.active:hover {
    color: var(--green-4);
}

.productPagePrice {
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-top: var(--offset-md2);
}

.productPagePrice span {
    font-size: 18px;
    line-height: 20px;
}

.productPagePrice strong {
    font-weight: 600;
    font-size: 24px;
}

.productPagePrice small {
    margin-left: var(--offset-xs);
    font-size: 18px;
}

.productPageRating {
    align-self: flex-end;
    display: flex;
    color: var(--yellow-6);
    margin-top: var(--offset-md2);
}

.productPageButton {
    align-self: flex-end;
    min-width: 180px;
    margin-top: var(--offset-md2);
}

@media screen and (min-width: 732px) and (max-width: 1055.98px) {
    .container {
        padding-top: 104px;
    }
    .productPageBody {
        margin-top: var(--offset-sm);
        margin-bottom: var(--offset-sm);
    }
    .productPageImg {
        flex: 0 0 400px;
        height: 400px;
    }
}

@media screen and (max-width: 731.98px) {
    .container {
        padding-top: 76px;
    }
    .productPageBody {
        flex-direction: column;
        margin-top: 0;
        margin-bottom: var(--offset-md);
    }
    .productPageImg {
        align-self: center;
        flex: auto;
        width: 280px;
        height: 280px;
        margin-right: 0;
    }
    .productPageRight {
        margin-top: var(--offset-sm);
    }
    .productPageLine {
        margin: var(--offset-sm) 0;
    }
    .productPagePrice {
        margin-top: var(--offset-sm);
    }
    .productPageRating {
        margin-top: var(--offset-sm);
    }

    .productPageButton {
        margin-top: var(--offset-sm);
    }
}
